import { createContact } from "../../../redux/contact/apiCalls";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";

export default function NewContact() {
    const { currentUser } = useSelector(state => state.user)

    const [inputs, setInputs] = useState({})
    const [file, setFile] = useState(null)
    const dispatch = useDispatch();
    const navigate = useNavigate()

    const handleChange = ({ target }) => {
        let { name, value } = target
        value = handleCat(name, value)
        setInputs((prev) => {
            return { ...prev, [name]: value };
        });
    };

    const handleSubmit = (ev) => {
        ev.preventDefault()
        createContact(dispatch, inputs, currentUser)
        navigate('/admin/contacts')
    }

    const handleCat = (name, value) => {
        if (name === 'categories') return value.split(",")
        else return value
    };

    const handleClick = (e) => {
        // e.preventDefault();
        // const fileName = new Date().getTime() + file.name;
        // const storage = getStorage(app);
        // const storageRef = ref(storage, fileName);
        // const uploadTask = uploadBytesResumable(storageRef, file);

        // Register three observers:
        // 1. 'state_changed' observer, called any time the state changes
        // 2. Error observer, called on failure
        // 3. Completion observer, called on successful completion
        // uploadTask.on(
        // "state_changed",
        // (snapshot) => {
        // Observe state change events such as progress, pause, and resume
        // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
        // const progress =
        // (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        // console.log("Upload is " + progress + "% done");
        // switch (snapshot.state) {
        // case "paused":
        // console.log("Upload is paused");
        // break;
        // case "running":
        // console.log("Upload is running");
        // break;
        // default:
        // }
        // },
        // (error) => {
        // Handle unsuccessful uploads
        // },
        // () => {
        // Handle successful uploads on complete
        // For instance, get the download URL: https://firebasestorage.googleapis.com/...
        // getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
        //     const product = { ...inputs, img: downloadURL, categories: cat };
        //     addProduct(product, dispatch);
        // });
        // }
        // );
    };

    return (
        <div className="newContact">
            <h1 className="addContactTitle">New Contact</h1>
            <form className="addContactForm" onSubmit={handleSubmit}>
                <div className="addContactItem">
                    <label>Image</label>
                    <input
                        type="file"
                        id="file"
                        onChange={(e) => setFile(e.target.files[0])}
                    />
                </div>
                <div className="addContactItem">
                    <label>Title</label>
                    <input
                        name="title"
                        type="text"
                        placeholder="Apple Airpods"
                        onChange={handleChange}
                    />
                </div>
                <div className="addContactItem">
                    <label>Description</label>
                    <input
                        name="desc"
                        type="text"
                        placeholder="description..."
                        onChange={handleChange}
                    />
                </div>
                <div className="addContactItem">
                    <label>Price</label>
                    <input
                        name="price"
                        type="number"
                        placeholder="100"
                        onChange={handleChange}
                    />
                </div>
                <div className="addContactItem">
                    <label>Categories</label>
                    <input type="text" placeholder="jeans,skirts" name="categories" onChange={handleChange} />
                </div>
                <div className="addContactItem">
                    <label>Stock</label>
                    <select name="inStock" onChange={handleChange}>
                        <option value="true">Yes</option>
                        <option value="false">No</option>
                    </select>
                </div>
                <button onClick={handleClick} className="addContactButton">
                    Create
                </button>
            </form>
        </div>
    );
}
